.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 2;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  min-width: 528px;
  min-height: 581px;
  z-index: 3;
  background: #fff;
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-btn {
  cursor: pointer;
  fill: #000;
  background: none;
  border: none;
  padding: 0;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-btn:hover, .modal-btn:focus {
  fill: #00f;
}

.modal-title {
  text-align: center;
  columns: var(--main-color);
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.form {
  width: 100%;
  margin: 0;
}

.form-label {
  width: 100%;
  margin-bottom: 28px;
  display: flex;
  position: relative;
}

.form-comment {
  margin-bottom: 20px;
}

.form-text {
  letter-spacing: .01em;
  color: var(--text-color);
  font-size: 12px;
  line-height: 1.17;
  position: absolute;
  top: -16px;
  left: 0;
}

.form-item {
  width: 18px;
  height: 18px;
  fill: var(--main-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  left: 12px;
  transform: translateY(50%);
}

.form-label:focus-within > .form-item {
  fill: var(--accent-color);
}

.form-input {
  width: 100%;
  height: 40px;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding: 0 0 0 42px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

textarea {
  resize: none;
  width: 100%;
  height: 120px;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding: 12px 16px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

.form-label:hover .form-input, .form-label:hover .form-item, .form-input:focus, textarea:hover, textarea:focus {
  border-color: var(--accent-color);
  fill: var(--accent-color);
}

.checkbox {
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.checkbox-input {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  position: absolute;
  left: 0;
}

.checkbox-item {
  width: 16px;
  height: 15px;
  cursor: pointer;
  border: solid 2px var(--main-color);
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
}

.checkbox-input:checked + .checkbox-item {
  background-color: var(--accent-color);
  border-color: var(--accent-color);
  background-origin: border-box;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.checkbox-agreement {
  color: var(--text-color);
  font-size: 14px;
  line-height: 1.71;
}

.checkbox-link {
  color: var(--accent-color);
  position: relative;
}

.checkbox-link:after {
  content: "";
  width: 100%;
  height: 1px;
  background: var(--accent-color);
  position: absolute;
  bottom: 1px;
  left: 0;
}

